      const h = 888
      const pw = this.widthPage / w
      const ph = this.heightPage / h
      const layer = [
        {
          m: [0 * pw, 455.80069 * ph],
          c1: [[207.496134 * pw, 296.133692 * ph], [381.866227 * pw, 323.394159 * ph], [523.110281 * pw, 537.582091 * ph]],
          c2: [[594.440214 * pw, 645.749555 * ph], [623.833854 * pw, 715.382455 * ph], [762.830106 * pw, 770.762231 * ph]],
          c3: [[956.826711 * pw, 818.444126 * ph], [1055.90156 * pw, 770.762231 * ph], [1142.11298 * pw, 770.762231 * ph]],
          c4: [[1192.77011 * pw, 770.762231 * ph], [1243.39912 * pw, 797.891323 * ph], [1294 * pw, 852.149506 * ph]],
          l1: [1294 * pw, 888 * ph],
          l2: [0 * pw, 888 * ph],
          l3: [0 * pw, 455.80069 * ph]
        },
        {
          m: [0 * pw, 396.616907 * ph],
          c1: [[186.22834 * pw, 190.739689 * ph], [349.964537 * pw, 194.895047 * ph], [491.208591 * pw, 409.082979 * ph]],
          c2: [[562.538524 * pw, 517.250442 * ph], [660.934109 * pw, 703.401618 * ph], [799.930361 * pw, 758.781394 * ph]],
          c3: [[950.879453 * pw, 818.923497 * ph], [1069.94216 * pw, 705.006945 * ph], [1156.15358 * pw, 705.006945 * ph]],
          c4: [[1206.81071 * pw, 705.006945 * ph], [1252.75952 * pw, 722.931762 * ph], [1294 * pw, 758.781394 * ph]],
          l1: [1294 * pw, 888 * ph],
          l2: [0 * pw, 888 * ph],
          l3: [0 * pw, 396.616907 * ph]
        },
        {
          m: [0 * pw, 319.570622 * ph],
          c1: [[196.119594 * pw, 91.134634 * ph], [342.194863 * pw, 58.9267274 * ph], [438.225809 * pw, 222.946902 * ph]],
          c2: [[593.822624 * pw, 488.705161 * ph], [563.251674 * pw, 763.042968 * ph], [702.247926 * pw, 818.422744 * ph]],
