          l3: [0 * pw, 319.570622 * ph]
        },
        {
          m: [795.911372 * pw, 0 * ph],
          c1: [[908.596261 * pw, 0 * ph], [1001.83432 * pw, 40.9246267 * ph], [1075.62555 * pw, 124.714709 * ph]],
          c2: [[1186.31239 * pw, 250.399834 * ph], [1294 * pw, 172.690477 * ph], [1294 * pw, 144.883241 * ph]],
          c3: [[1294 * pw, 126.345084 * ph], [1294 * pw, 77.8627159 * ph], [1294 * pw, 0 * ph]],
          l1: [795.911372 * pw, 0 * ph]
        },
        {
          m: [795.911372 * pw, 0 * ph],
          c1: [[994.970972 * pw, 3.68120779 * ph], [1083.34776 * pw, 47.299697 * ph], [1061.04175 * pw, 130.290293 * ph]],
          c2: [[1027.58273 * pw, 254.776188 * ph], [1294 * pw, 130.515151 * ph], [1294 * pw, 102.707521 * ph]],
          c3: [[1294 * pw, 84.1691002 * ph], [1294 * pw, 49.7448685 * ph], [1294 * pw, 0 * ph]],
          l1: [795.911372 * pw, 0 * ph]
        }
      ]
      const layer1 = layer[0], layer2 = layer[1], layer3 = layer[2], layer4 = layer[3], layer5 = layer[4]
      const objStyles = [
        {
          'clip-path': `path("M${layer1['m'][0]},${layer1['m'][1]} C${layer1['c1'][0][0]},${layer1['c1'][0][1]} ${layer1['c1'][1][0]},${layer1['c1'][1][1]} ${layer1['c1'][2][0]},${layer1['c1'][2][1]} C${layer1['c2'][0][0]},${layer1['c2'][0][1]} ${layer1['c2'][1][0]},${layer1['c2'][1][1]} ${layer1['c2'][2][0]},${layer1['c2'][2][1]} C${layer1['c3'][0][0]},${layer1['c3'][0][1]} ${layer1['c3'][1][0]},${layer1['c3'][1][1]} ${layer1['c3'][2][0]},${layer1['c3'][2][1]} C${layer1['c4'][0][0]},${layer1['c4'][0][1]} ${layer1['c4'][1][0]},${layer1['c4'][1][1]} ${layer1['c4'][2][0]},${layer1['c4'][2][1]} L${layer1['l1'][0]},${layer1['l1'][1]} L${layer1['l2'][0]},${layer1['l2'][1]} L${layer1['l3'][0]},${layer1['l3'][1]} Z")`
        },
        {
          'clip-path': `path("M${layer2['m'][0]},${layer2['m'][1]} C${layer2['c1'][0][0]},${layer2['c1'][0][1]} ${layer2['c1'][1][0]},${layer2['c1'][1][1]} ${layer2['c1'][2][0]},${layer2['c1'][2][1]} C${layer2['c2'][0][0]},${layer2['c2'][0][1]} ${layer2['c2'][1][0]},${layer2['c2'][1][1]} ${layer2['c2'][2][0]},${layer2['c2'][2][1]} C${layer2['c3'][0][0]},${layer2['c3'][0][1]} ${layer2['c3'][1][0]},${layer2['c3'][1][1]} ${layer2['c3'][2][0]},${layer2['c3'][2][1]} C${layer2['c4'][0][0]},${layer2['c4'][0][1]} ${layer2['c4'][1][0]},${layer2['c4'][1][1]} ${layer2['c4'][2][0]},${layer2['c4'][2][1]} L${layer2['l1'][0]},${layer2['l1'][1]} L${layer2['l2'][0]},${layer2['l2'][1]} L${layer2['l3'][0]},${layer2['l3'][1]} Z")`
        },
        {
          'clip-path': `path("M${layer3['m'][0]},${layer3['m'][1]} C${layer3['c1'][0][0]},${layer3['c1'][0][1]} ${layer3['c1'][1][0]},${layer3['c1'][1][1]} ${layer3['c1'][2][0]},${layer3['c1'][2][1]} C${layer3['c2'][0][0]},${layer3['c2'][0][1]} ${layer3['c2'][1][0]},${layer3['c2'][1][1]} ${layer3['c2'][2][0]},${layer3['c2'][2][1]} C${layer3['c3'][0][0]},${layer3['c3'][0][1]} ${layer3['c3'][1][0]},${layer3['c3'][1][1]} ${layer3['c3'][2][0]},${layer3['c3'][2][1]} L${layer3['l1'][0]},${layer3['l1'][1]} L${layer3['l2'][0]},${layer3['l2'][1]} L${layer3['l3'][0]},${layer3['l3'][1]} Z")`
        },
        {
          'clip-path': `path("M${layer4['m'][0]},${layer4['m'][1]} C${layer4['c1'][0][0]},${layer4['c1'][0][1]} ${layer4['c1'][1][0]},${layer4['c1'][1][1]} ${layer4['c1'][2][0]},${layer4['c1'][2][1]} C${layer4['c2'][0][0]},${layer4['c2'][0][1]} ${layer4['c2'][1][0]},${layer4['c2'][1][1]} ${layer4['c2'][2][0]},${layer4['c2'][2][1]} C${layer4['c3'][0][0]},${layer4['c3'][0][1]} ${layer4['c3'][1][0]},${layer4['c3'][1][1]} ${layer4['c3'][2][0]},${layer4['c3'][2][1]} L${layer4['l1'][0]},${layer4['l1'][1]} Z")`
        },
        {
          'clip-path': `path("M${layer5['m'][0]},${layer5['m'][1]} C${layer5['c1'][0][0]},${layer5['c1'][0][1]} ${layer5['c1'][1][0]},${layer5['c1'][1][1]} ${layer5['c1'][2][0]},${layer5['c1'][2][1]} C${layer5['c2'][0][0]},${layer5['c2'][0][1]} ${layer5['c2'][1][0]},${layer5['c2'][1][1]} ${layer5['c2'][2][0]},${layer5['c2'][2][1]} C${layer5['c3'][0][0]},${layer5['c3'][0][1]} ${layer5['c3'][1][0]},${layer5['c3'][1][1]} ${layer5['c3'][2][0]},${layer5['c3'][2][1]} L${layer5['l1'][0]},${layer5['l1'][1]} Z")`
        }
      ]
      return objStyles
    }
  },

  methods: {
    handleSend() {
      if (this.mail) {
        let that = this
        this.loading = true
        axios({
          method: 'post',
          url: 'https://krx.kisvn.vn/rta/team/assign',
          data: {
            email: `${this.mail}@kisvn.vn`
          }
        })
          .then((res) => {
            if (res.data[0]) {
              that.member = res.data[0]
              let { team, number_of_spins } = res.data[0]
              axios({
                method: 'get',
                url: `https://krx.kisvn.vn/rta/team/${team}`
              })
                .then(function (data) {
                  that.loading = false
                  that.tableData = data.data
                });
              if (number_of_spins === 0) {
                that.disabled = false
                that.team = team
              } else {
                const { name } = this.list.find(item => item.id === team)
                that.teamName = name
              }
            } else {
              that.loading = false
              that.$alert("I'm sorry, but you are not on the registration list.", 'Error!', {
                confirmButtonText: 'Cancel',
              });
            }
          })
          .catch((err) => {
            that.loading = false
            that.$notify.error({
              title: 'Error',
              message: err.message
            });
          });
      } else {
        this.requireMail = true
      }
    },
    outTeam(data) {
      this.disabled = true
      this.teamName = data
      this.tabActive = 'assigned'
    }
  }
}
</script>
