<template>
    <FortuneWheel style="width: 96%; margin: 20px 2%;" :canvas="canvasOptions" :prizes="prizes" :prize-id="team"
        :verify="cavansVerify" :disabled="disabled" :class="[disabled ? 'disabled' : '']" @rotateStart="onCanvasRotateStart"
        @rotateEnd="onRotateEnd" />
</template>
  
<script>
import FortuneWheel from 'vue-fortune-wheel'
import 'vue-fortune-wheel/lib/vue-fortune-wheel.css'
export default {
    name: 'LuckyWheel',
    components: { FortuneWheel },
    props: ['disabled', 'team', 'list'],
    data() {
        return {
            cavansVerify: true, // Whether the turntable in canvas mode is enabled for verification
            canvasOptions: {
                borderWidth: 6,
                borderColor: '#584b43'
            },
            prizes: this.$props.list
        }
    },
    methods: {
        onCanvasRotateStart(rotate) {
            if (this.cavansVerify) {
                const verified = true // true: the test passed the verification, false: the test failed the verification
                this.DoServiceVerify(verified, 1).then((verifiedRes) => {
                    if (verifiedRes) {
                        console.log('Verification passed, start to rotate')
                        rotate() // Call the callback, start spinning
                        this.cavansVerify = false // Turn off verification mode
                    } else {
                        console.log('Failed verification')
                    }
                })
                return
            }
        },
        onRotateEnd(prize) {
            let that = this
            this.$alert(`You have been assigned to team ${prize.value}`, 'Congratulations!', {
              confirmButtonText: 'Next',
              callback: () => {
                that.$emit('outTeam', prize.value)
              }
            });
        },
        // Simulate the request back-end interface, verified: whether to pass the verification, duration: delay time
        DoServiceVerify(verified, duration) {
            return new Promise((resolve) => {
                setTimeout(() => {
                    resolve(verified)
                }, duration)
            })
        }
    }
}
</script>

<style lang='scss'>
.fw-btn__btn {
    cursor: pointer;
}
.disabled {
    .fw-btn__btn {
        cursor: not-allowed;
    }
}

</style>
  